<style>
#TransportDetail .details_title {
  line-height: 24px;
  border-left: 5px solid #2d8cf0;
  font-weight: bold;
  font-size: 16px;
  text-indent: 10px;
}
#TransportDetail .info_box {
  width: 100%;
  min-height: 200px;
  background: rgb(245, 247, 249);
  margin: 15px 0px;
  text-indent: 20px;
}
#TransportDetail .infoitem {
  min-height: 45px;
  line-height: 45px;
  font-size: 14px;
}
#TransportDetail .infoitem label {
  padding-right: 15px;
}
#TransportDetail .RadioGroupbox{
  padding: 15px 0;
}
/* .TransportDetail >>> .ivu-table-cell {
  padding-left: 0px;
  padding-right: 0px;
}
.PartNumberbox p {
  border-bottom: 1px solid #dddddd;
  min-height: 40px;
  line-height: 39px;
}
.PartNumberbox p:nth-last-child() {
  border: none;
}
.otderstyle .PartNumberbox {
  display: table;
  width: 100%;
  min-height: 40px;
}
.PartNumberbox ul li {
  border-bottom: 1px solid #dddddd;
  min-height: 40px;
}
.otderstyle .tablespan {
  display: table-cell;
  vertical-align: middle;
  border-bottom: 1px solid #dddddd;
} */

 #TransportDetail .subCol ul li {
  margin: 0 -18px;
  list-style: none;
  text-align: center;
  padding: 9px;
  border-bottom: 1px solid #ccc;
  overflow-x: hidden;
}
#TransportDetail .subCol ul li:last-child {
  border-bottom: none;
}
</style>
<template>
  <div class="TransportDetail" id="TransportDetail">
    <p class="details_title">基本信息</p>
    <div class="info_box">
      <Row v-if="detailinfo!=null">
        <Col span="9">
          <p class="infoitem">
            <label class>运输批次：</label>
            <span class>{{detailinfo.LotNumber}}
               <Button v-if="detailinfo.ID!=null" icon='md-calendar' type="success" size='small' @click="logchange">日志管理</Button>
            </span>
          </p>
          <!-- <p class="infoitem">
            <label class>状&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;态：</label>
            <span class>可出库</span>
          </p>-->
          <p class="infoitem">
            <label class>承&nbsp;&nbsp;运&nbsp;&nbsp;商：</label>
            <span class>{{detailinfo.CarrierName}}</span>
          </p>
          <p class="infoitem">
            <label class>司&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;机：</label>
            <span class>{{detailinfo.Driver}}</span>
          </p>
          <p class="infoitem">
            <label class>联系电话：</label>
            <span class>{{detailinfo.Phone}}</span>
          </p>
          <p class="infoitem">
            <label class>运输时间：</label>
            <span class>{{detailinfo.DepartDate|showDate}}</span>
          </p>
          <p class="infoitem">
            <label class>运输类型：</label>
            <span v-if="detailinfo.IsOnevehicle==true">包车</span>
            <span v-if="detailinfo.IsOnevehicle==false">普通</span>
          </p>
        </Col>
        <Col span="6">
          <!--<p class="infoitem">
             <label class="">运输时间：</label>
            <span class="">2019-12-7</span>
          </p>-->
          <p class="infoitem">
            <label class>截单状态：</label>
             <span v-if="detailinfo.CuttingOrderStatus==0">等待</span>
             <span v-if="detailinfo.CuttingOrderStatus==1">已截单</span>
             <span v-if="detailinfo.CuttingOrderStatus==2">已完成</span>
          </p>
          <p class="infoitem">
            <label class>总&nbsp;&nbsp;数&nbsp;&nbsp;量：</label>
            <span class>{{detailinfo.TotalQuantity}}</span>
          </p>
          <p class="infoitem">
            <label class>总&nbsp;&nbsp;条&nbsp;&nbsp;数：</label>
            <span class>{{detailinfo.TotalRecord}}</span>
          </p>
          <p class="infoitem">
            <label class>总&nbsp;&nbsp;金&nbsp;&nbsp;额：</label>
            <span class>{{detailinfo.Currency}}{{detailinfo.TotalPrice}}</span>
          </p>
          <p class="infoitem">
            <label class>总毛重(kg)：</label>
            <span class>{{detailinfo.TotalWeight}}</span>
          </p>
          <p class="infoitem">
            <label class>总&nbsp;&nbsp;箱&nbsp;&nbsp;数：</label>
            <span class>{{detailinfo.TotalParts}}</span>
          </p>
        </Col>
        <Col span="6">
          <!-- 根据最新需求，不做打印处理 -->
          <!-- <Button type="success">清单打印</Button> -->
          <div v-if="detailinfo!=null">
            <Button
              type="success"
              style="margin-top:20px;"
              :disabled="disabledIs==true?true:false"
              @click="isconfirm=true"
            >完成出库</Button>
          </div>
        </Col>
      </Row>
    </div>
    <p class="details_title">运输清单</p>
    <div class="tablebox"></div>
    <div>
      <div>
        <div class="RadioGroupbox">
          <label>订单类型：</label>
          <RadioGroup v-model="disabledGroup" @on-change='changeradio'>
              <Radio label="0">全部</Radio>
              <Radio label="1">内单</Radio>
              <Radio label="2">外单</Radio>
          </RadioGroup>
        </div>
        <Table :columns="titlelable" :data="Notices" :border="Notices.length > 0">
            <template slot-scope="{ row, index }" slot="total">
               {{row.BoxesNotices.length}}
            </template>
            <template slot-scope="{ row, index }" slot="boxcode">
              <div class="subCol">
                <ul>
                  <li v-for="item in row.PackDate" >{{item.BoxCode|showboxcode}}</li>
                </ul>
              </div>
            </template>
            
        </Table>
      </div>
    </div>
    <Modal v-model="isconfirm" title="提示" @on-ok="ok_confirm" @on-cancel="cancel">
      <p>是否确认出库</p>
    </Modal>
      <!-- 操作日志 -->
     <Modal
        v-model="showlogged"
        :footer-hide='true'
        :mask-closable='false'
        width='60'>
        <div slot="close">
          <Icon style="font-size:21px;color:#cccccc;padding-top: 5px" type="ios-close-circle-outline" />
        </div>
        <div slot="header">
          <span style="font-size:18px;color:#1aaff7;">日志管理</span>
        </div>
        <logg-ed ref="logged" :key='loggdetime' v-if="showlogged" :WaybillID='detailinfo.ID'></logg-ed>
    </Modal>
    <!-- 操作日志 -->
  </div>
</template>
<script>
// import expandRow from "./table-expand.vue";
// import {TransportDetail,OutputEnter,CgDelcareShipDetail} from "../../api";
import logged from '../Common/logged'
import { CgDelcareShipDetail, CgAutoHkExit } from "../../api/CgApi";
export default {
  components: {
    'logg-ed':logged
  },
  data() {
    return {
      loggdetime:'',//操作日志时间
      showlogged:false,//操作日志
      isconfirm: false, //是否确认出库
      lotnumber: this.$route.params.id, //运输批次号
      houseid: sessionStorage.getItem("UserWareHouse"),
      tableData: [
        {
          boxnumber: "Nl1919979", //箱号
          createdata: "2019-11-4", //封箱时间
          Packingperson: "张三", //封箱人
          orderid: "11111", //订单编号
          clientID: "11111", //客户编号
          clientName: "11111", //客户名称
          spanlen: 3
        },
        {
          boxnumber: "Nl1919979",
          createdata: "2019-11-4",
          Packingperson: "张三",
          orderid: "orderid333",
          clientID: "Nl898989",
          clientName: "远大电子科技有限公司",
          spanlen: 3
        },
        {
          boxnumber: "Nl1919979",
          createdata: "2019-11-4",
          Packingperson: "张三",
          orderid: "orderid333",
          clientID: "Nl898989",
          clientName: "远大电子科技有限公司",
          spanlen: 3
        },
        {
          boxnumber: "Nl1919979",
          createdata: "2019-11-4",
          Packingperson: "张三",
          orderid: "adasdd",
          clientID: "Nl8989891",
          clientName: "杭州比一比电子科技有限公司",
          spanlen: 3
        },
        {
          boxnumber: "Nl1919979",
          createdata: "2019-11-4",
          Packingperson: "张三",
          orderid: "adasdd",
          clientID: "Nl8989891",
          clientName: "杭州比一比电子科技有限公司",
          spanlen: 3
        }
      ],
      columns1: [
        {
          title: "#",
          slot: "index",
          align: "center"
        },
        {
          title: "箱号",
          slot: "boxnumber",
          align: "center"
        },
        {
          title: "装箱时间",
          slot: "boxtimer",
          align: "center"
        },
        {
          title: "装箱人",
          slot: "boxperson",
          align: "center"
        },
        {
          title: "订单编号",
          slot: "orderID",
          align: "center",
          width: 140
        },
        {
          title: "型号",
          slot: "PartNumber",
          align: "center"
        },
        {
          title: "制造商",
          slot: "Manufacturer",
          align: "center"
        },
        {
          title: "封装",
          slot: "PackageCase",
          align: "center"
        },
        {
          title: "批次",
          slot: "DateCode",
          align: "center"
        },
        {
          title: "应到小计",
          slot: "Total",
          align: "center"
        },
        {
          title: "客户编号",
          slot: "ClientID",
          align: "center"
        },
        {
          title: "客户名称",
          slot: "ClientName",
          align: "center"
        }
      ],
      data1: [
        {
          boxnumber: "第一箱",
          boxtimer: "2019-12-15",
          boxperson: "张三",
          order: [
            {
              orderID: "12121212",
              product: [
                {
                  CreateDate: "/Date(1578474657693)/",
                  ID: "C4713C21258938F4D060B62C11511FB1",
                  Manufacturer: "deng100",
                  PackageCase: "null",
                  Packaging: "null",
                  PartNumber: "deng200",
                  DateCode: "批次",
                  Total: 200
                },
                {
                  CreateDate: "/Date(1578474657693)/",
                  ID: "C4713C21258938F4D060B62C11511FB1",
                  Manufacturer: "deng100",
                  PackageCase: "null",
                  Packaging: "null",
                  PartNumber: "deng200",
                  DateCode: "批次",
                  Total: 200
                }
              ]
            },
            {
              orderID: "12121212",
              product: [
                {
                  CreateDate: "/Date(1578474657693)/",
                  ID: "C4713C21258938F4D060B62C11511FB1",
                  Manufacturer: "deng100",
                  PackageCase: "null",
                  Packaging: "null",
                  PartNumber: "deng200",
                  DateCode: "批次",
                  Total: 200
                }
              ]
            }
          ],
          ClientID: "BL767567",
          ClientName: "杭州比一比"
        },
        {
          boxnumber: "第一箱",
          boxtimer: "2019-12-15",
          boxperson: "张三",
          order: [
            {
              orderID: "12121212",
              product: [
                {
                  CreateDate: "/Date(1578474657693)/",
                  ID: "C4713C21258938F4D060B62C11511FB1",
                  Manufacturer: "deng100",
                  PackageCase: "null",
                  Packaging: "null",
                  PartNumber: "deng200",
                  DateCode: "批次",
                  Total: 200
                }
              ]
            }
          ],
          ClientID: "BL767567",
          ClientName: "杭州比一比"
        }
      ],
      detailinfo: null, //详情信息
      titlelable: [
          {
            type: 'index',
            width: 60,
            align: 'center'
          },
          {
            title: "订单号",
            key: "TinyOrderID"
          },
          {
            title: "客户编号",
            key: "EnterCode"
          },
          {
            title: "客户名称",
            key: "ClientName"
          },
          {
            title: "总条数",
            key: "Count",
            width:100
          },
          {
            title: "箱号",
            slot: "boxcode",
            align: "center",
            // render: (h, params) => {
            //   return h(
            //     "div",
            //     {
            //       attrs: {
            //         class: "subCol"
            //       }
            //     },
            //     [
            //       h(
            //         "ul",
            //         this.Notices[params.index].PackDate.map(item => {
            //           return h("li", {}, item.BoxCode);
            //         })
            //       )
            //     ]
            //   );
            // }
          },
          {
            title: "装箱时间",
            key: "list",
            align: "center",
            render: (h, params) => {
              return h(
                "div",
                {
                  attrs: {
                    class: "subCol"
                  }
                },
                [
                  h(
                    "ul",
                    this.Notices[params.index].PackDate.map(item => {
                      return h("li", {}, item.PackDate);
                    })
                  )
                ]
              );
            }
          },
      ],
      Notices: [],//绑定数据
      AllData:[],//全部数据
      innerData:[],//内单数据
      externalData:[],//外单数据
      disabledGroup:"0",//默认选中的值
      disabledIs:true,
    };
   
  },
  created() {
    console.log(this.$route);
  },
  mounted() {
    this.getdetail();
  },
  methods: {
    getdetail() {  //获取详情页面数据
      CgDelcareShipDetail(this.lotnumber).then(res => {
        console.log(res);
        if(res.CuttingOrderStatus==1){
          this.disabledIs=false;
        }else{
           this.disabledIs=true;
        }
        this.detailinfo = res;
        this.Notices=res.Notices;
        this.AllData=res.Notices;
        for(var i=0,len=res.Notices.length;i<len;i++){
          if(res.Notices[i].Source==35){
              this.innerData.push(res.Notices[i])
          }else{
              this.externalData.push(res.Notices[i])
          }
        }

      });
    },
    ok_confirm() { //确认出库
      this.disabledIs=true;
      var adminID=sessionStorage.getItem("userID")
      CgAutoHkExit(this.detailinfo.LotNumber,adminID).then(res => {
        if (res.success == true&&res.code==200) {
          this.$Message.success("出库完成");
          var _this=this
          setTimeout(function(){
             _this.$store.dispatch("setTransportDetail", false);
          },1000)
         
        }else {
          this.$Message.error("出库失败");
        }
         this.disabledIs=false;
      });
    },
    cancel() { //取消出库
      this.isconfirm = false;
    },
    changeradio(value){
      console.log(value)
      if(value=="0"){
         this.Notices=this.AllData
      }else if(value=="1"){
           this.Notices=this.innerData
      }else{
          this.Notices=this.externalData
      }
    },
    //操作日志的展示
    logchange(){
      this.showlogged=true
      this.loggdetime=new Date().getTime()
    },
  }
};
</script>


